import React from 'react';
import { graphql, Link, useStaticQuery } from "gatsby";
import './aside.less';

const Aside = (props) => {
    const data = useStaticQuery(graphql`
    query MyQuery {
        allMarkdownRemark {
          edges {
            node {
              frontmatter {
                path
                title
              }
            }
          }
        }
      }`);
    const path = decodeURI(document.location.pathname);
    return (
        <aside className='aside-wrap'>
            <div className={path === '/blog' ? 'active': ''}>å‰è¨€</div>
            {
                data && data.allMarkdownRemark.edges.map((edge, index) => (
                    <Link
                      key={index}
                      to={edge.node.frontmatter.path}
                      className={path === edge.node.frontmatter.path ? 'active': ''}
                    >
                      {edge.node.frontmatter.title}
                    </Link>
                ))
            }
        </aside>
    )
};
export default Aside;
